import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Button,TextField } from '@material-ui/core'
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import { makeStyles } from '@material-ui/core/styles';


export default function Form({vacations}) {

    const history = useHistory()
    // ======================================================
    const params = useParams()
    // ======================================================
    const [destination, setDestination] = useState("")
    const [description, setDescription] = useState("")
    const [image, setImage] = useState("")
    const [start_date, setStartDate] = useState("")
    const [end_date, setEndDate] = useState("")
    const [price, setPrice] = useState("")

    
    const useStyles = makeStyles(() => ({
        mb: {
          margin: 5
        }
    }));

    const classes = useStyles();

    async function add (){
        try{
            if (!description || !destination || !image || !start_date || !end_date || !price) {
                alert('all fields must be full')
                throw new Error('all fields must be filled out')
            }
            if (price < 0 || isNaN(price)) {
                console.log('yea wat');
                document.getElementById('component-error-text').hidden=false
                throw new Error('price must be a number')
            }
            const res = await fetch('/data/add', {
                method:'POST',
                body: JSON.stringify({
                    description,
                    destination,
                    image,
                    start_date,
                    end_date,
                    price
                }),
                headers:{
                    'Content-Type':'application/json',
                    'Authorization':localStorage.token
                }
            })
            if (res.status === 201) {
                history.push('/')    
            }
        }
        catch (error){
            console.error(error);
        }
    }
    
    async function edit (){
        try{
            if (!description || !destination || !image || !start_date || !end_date || !price) {
                alert('all fields must be full')
                throw new Error('all fields must be filled out')
            }
            if (price < 0 || isNaN(price)) {
                console.log('yea wat');
                document.getElementById('component-error-text').hidden=false
                throw new Error('price must be a number')
            }
            const res = await fetch('/data/' + params.vacnum, {
                method:'PUT',
                body: JSON.stringify({
                    description,
                    destination,
                    image,
                    start_date,
                    end_date,
                    price
                }),
                headers:{
                    'Content-Type':'application/json',
                    'Authorization':localStorage.token
                }
            })
            console.log(res);
            if (res.status === 200) {
                history.push('/')    
            }
        }
        catch (error){
            console.log(error);
        }
    }

    useEffect(() => {
        console.log(vacations);
        if (params.vacnum && vacations.length) {
            const vacation = vacations.find(vac => vac.id == params.vacnum)
            if (vacation) {   
                setDestination(vacation.destination)
                setDescription(vacation.description)
                setImage(vacation.image)
                setStartDate(vacation.start_date.substring(0, 10))
                setEndDate(vacation.end_date.substring(0, 10))
                setPrice(vacation.price)
            }else{
                history.push('/')
            }
        }else{
            setDestination("")
            setDescription("")
            setImage("")
            setStartDate("")
            setEndDate("")
            setPrice("")
        }
    }, [vacations])

    return (
        <div className="form">
            <div className="formsep">
            <TextField className={classes.mb} label="Destination" value={destination} onChange={(e) => {setDestination(e.target.value)}}/>
            <TextField className={classes.mb} label="Description" id="description" value={description} onChange={(e) => {setDescription(e.target.value)}}/>
            <TextField className={classes.mb} label="Image address" value={image} onChange={(e) => {setImage(e.target.value)}}/>
            </div>
            <div className="formdate">
            <TextField id="start" label="Vacation start" type="date" value={start_date} onChange={(e) => {setStartDate(e.target.value)}} InputLabelProps={{shrink: true,}}/>
            <TextField id="end" label="Vacation end" type="date" value={end_date} onChange={(e) => {setEndDate(e.target.value)}} InputLabelProps={{shrink: true,}}/>
            </div>
            <div className="formsep">
            <FormControl error>
            <TextField className={classes.mb} label="Price" value={price} onChange={(e) => {setPrice(e.target.value);document.getElementById('component-error-text').hidden=true}}/>
            <FormHelperText hidden id="component-error-text">Use numbers only!</FormHelperText>
            </FormControl>
            <Button variant="contained" color="primary" onClick={params.vacnum ? edit : add}>
                Complete
            </Button>
            </div>
        </div>
    )
}
