import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import jwt from 'jsonwebtoken'
import { useHistory } from 'react-router'
import { Switch,Route } from 'react-router'
import List from './List'
import Form from './Form'
import Charting from './Charting'
import { Button } from '@material-ui/core'


export default function Main() {

    
    const [vacations, setVacations] = useState([])
    const [refresh, setRefresh] = useState(false)
    
    const fname = useSelector(state => state.fname)
    const auth = useSelector(state => state.auth)
    const dispatch = useDispatch()
    const history = useHistory()



    // check user (token or redirect)
    useEffect(() => {
        (async () => {
            try{

                if (localStorage.token) {
                    const payload = await jwt.verify(localStorage.token, process.env.REACT_APP_JWT_CODE)
                    await dispatch({
                        type:"SET_USER",
                        value:payload.id
                    })
                    await dispatch({
                        type:"SET_FNAME",
                        value:payload.f_name
                    })
                    if (payload.type) {  
                        await dispatch({
                            type:"SET_AUTH",
                        })
                        const res = await fetch('/data/chart', {
                            method:"GET",
                            headers: {"Authorization":`${localStorage.token}`}
                        })
                        const data = await res.json()
                        setVacations(data.dbdata)
                    } 
                    else{
                        await dispatch({
                            type:"REMOVE_AUTH",
                        })
                        const res = await fetch('/data', {
                            method:"GET",
                            headers: {"Authorization":`${localStorage.token}`}
                        })
                        const data = await res.json()
                        const Arr = data.dbdata.map(e=>e)
                        setVacations(Arr)
                    }
                }else {
                    history.push('/login')
                }
            }
            catch (error) {
                if (error.name === "TokenExpiredError") {
                    console.log("your session has expired, please login again");
                }
                history.push('/login')
                    
                console.log(error);
            }
        })()
    }, [refresh])

    function update () {
        setRefresh(!refresh)
    }

    return (
        <div className="main">
            <div className="header">
                {/* logo */}
                <div className="fl">
                    <img src="logo.png" alt="vacater logo" />
                    {/* navbar */}
                    <div className="navbar">
                    {auth ? 
                    <div className="adminnav">
                        <Button onClick={()=>{history.push("/")}}>Home</Button>
                        <Button onClick={()=>{history.push("/form")}}>New Vacation</Button>
                        <Button onClick={()=>{history.push("/charts")}}>Charts</Button>
                    </div>
                    : 
                    <p>Happy Browsing!</p>
                    }
                    </div>
                </div>
                {/* user */}
                <div className="div">
                    <p>Hello {fname}!</p>
                    <Button variant="contained" color="primary" onClick={()=>{
                        localStorage.removeItem('token')
                        history.push('/login')
                    }}>
                    Logout
                    </Button>
                </div>
            </div>
           

            <Switch>
                <Route path='/' exact>
                    <List vacations={vacations} update={update}/>
                </Route>
                <Route path='/form/:vacnum'>
                    <Form vacations={vacations}/>
                </Route>
                <Route path='/form' exact>
                    <Form />
                </Route>
                <Route path='/charts'>
                    <Charting vacations={vacations}/>
                </Route>
            </Switch>
            <div className="footer">
            <div>Logo made by <a href="https://www.designevo.com/" title="Free Online Logo Maker">DesignEvo free logo creator</a></div>
            </div>
        </div> 
    )
}
