import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { ButtonGroup,Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';



export default function Item({vacation, update}) {

  const history = useHistory()
  const auth = useSelector(state => state.auth)
  const user = useSelector(state => state.user)
  const [followed, setFollowed] = useState(false) 
      
  const useStyles = makeStyles({
      root: {
        maxWidth: 345,
      },
      media: {
        height: 140,
      },
  });

  
  const classes = useStyles();


  useEffect(() => {
      if (user === vacation.user_id) {
          setFollowed(true)
      }
  }, [])

  async function followFunc () {
    try {
      const res = await fetch('/data/follow/' + vacation.id, {
        headers: {"Authorization":`${localStorage.token}`}
      })
      if (res.status === 201) {
        setFollowed(true)
      }
      if (res.status === 204) {
        setFollowed(false)
      }
    }
    catch (error) {
        console.log(error);
    }
  }

  async function delFunc () {
    const res = await fetch('/data/' + vacation.id, {
      method:"DELETE",
      headers: {"Authorization":`${localStorage.token}`}
    })
    if (res.status === 204) {
      update()
    }else {
      console.log(res);
    }
  }

  return (
    <div className="listcomponent">
      <Card className={classes.root}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            style={{height: 0, paddingTop: '56.25%'}}
            image={`${vacation.image}`}
            title="Vacation Destination"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {vacation.destination}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              {vacation.description}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
        <Typography variant="h6" component="h4">
          {vacation.price}$
        </Typography>
          {auth ? <ButtonGroup color="primary">
                      <Button onClick={()=>{history.push(`/form/${vacation.id}`)}}><EditIcon /></Button>
                      <Button onClick={delFunc}><DeleteForeverIcon /></Button>
                  </ButtonGroup>
          :   <Button onClick={followFunc}>{followed ? <FavoriteIcon color="primary" /> : <FavoriteBorderIcon />}</Button>}
        </CardActions>
      </Card>  
    </div>
  )
}
