import React, { useState } from 'react'
import { useHistory,Link } from 'react-router-dom'
import { Button,TextField } from '@material-ui/core'


export default function Register() {

    const [username, setUsername] = useState("")
    const [f_name, setFname] = useState("")
    const [l_name, setLname] = useState("")
    const [password, setPassword] = useState("")
    const history = useHistory()


    async function register () {
        try{
            if (!username || !f_name || !l_name || !password) {
                throw new Error('all fields must be full')
            }
            const res = await fetch('/users/register',{
                method:'POST',
                body:JSON.stringify({
                    username,
                    f_name,
                    l_name,
                    password
                }),
                headers:{"Content-Type":"application/json"}
            })
            if (res.status === 200) {
                const {token} = await res.json()
                localStorage.token = token
                // ====================================================== 
                history.push('/')
            }
            if (res.status === 403) {
                setUsername("")
                alert('username is taken')
            }
        }catch (error){
            if (error ='all fields must be full') {
                alert('all fields must be full')
            }
        }

    }

    return (
        <div className="centerdiv">
        <div className="logindiv">
            <TextField label="Username" value={username} onChange={(e) => {setUsername(e.target.value)}}/>
            <TextField label="Password" type="password" value={password} onChange={(e) => {setPassword(e.target.value)}}/>
            <TextField label="First name" value={f_name} onChange={(e) => {setFname(e.target.value)}}/>
            <TextField label="Last name" value={l_name} onChange={(e) => {setLname(e.target.value)}}/>

            <Button variant="contained" color="primary" onClick={(register)}>
                Register
            </Button>
            <p>already have an account? <Link to={"/login"}>Login</Link> </p>
        </div>
        </div>
    )
}
