import React, { useEffect } from 'react'
import Item from './Item';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
}));

export default function List({vacations, update}) {

    const classes = useStyles();


    useEffect(() => {
        update();
    }, [])

    return (
        <div className={classes.root} id="listcont">
            <Grid container spacing={3}>
            {vacations.map(vacation => 
            <Grid item xs={12} sm={6} md={4} lg={3} key={vacation.id}>
            <Item className={classes.paper} vacation={vacation} key={vacation.id} update={update}/>
            </Grid>)}
            </Grid>
        </div>
    )
}
