export const userReducer = (state = 0, action) => {
    switch (action.type) {
      case "SET_USER":
        return state = action.value;
      case "REMOVE_USER":
        return state = 0;
      default:
        return state;
    }
  };
  
  export const fnameReducer = (state = [""], action) => {
    switch (action.type) {
      case "SET_FNAME":
        return state = action.value;
      case "REMOVE_NAME":
        return state = "";
      default:
        return state;
    }
  };
  
  export const authReducer = (state = false, action) => {
    switch (action.type) {
      case "SET_AUTH":
        return state = true;
      case "REMOVE_AUTH":
        return state = false;
      default:
        return state;
    }
  };
  